body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

main {
  flex: 1 1 auto;
}

html {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  margin: 0;
  padding: 0;
  height: 100vh;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff") format("woff");
  src: url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff") format("woff");
  src: url("../fonts/opensansbold.woff2") format("woff2");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}

.button {
  display: block;
  width: 260px;
  height: 49px;
  padding: 18px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: inherit;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
  border: none;
  outline: 2px solid #444444;
  cursor: pointer;
}
.button:hover {
  color: #ffffff;
  background: #444444;
}
.button:focus {
  color: #ffffff;
  background: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background: #444444;
}
.button--secondary {
  padding: 18px;
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  background-color: #63d1bb;
  outline: none;
}
.button--secondary:hover {
  background-color: #63d1bb;
  opacity: 0.8;
}
.button--secondary:focus {
  background-color: #63d1bb;
  outline: auto;
  opacity: 0.7;
}
.button--secondary:active {
  background-color: #63d1bb;
  opacity: 0.6;
}
.button--modal:hover {
  color: #444444;
  background-color: transparent;
  outline-color: rgba(68, 68, 68, 0.3);
}
.button--modal:active {
  outline-color: #62d1ba;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}

.container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
}

.categories {
  position: relative;
  width: 100%;
  background-color: #f9f9f9;
}
@media (min-width: 768px) {
  .categories {
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .categories__zig-zag::after {
    content: "";
    position: absolute;
    top: 97%;
    display: block;
    width: 100%;
    height: 24px;
    background-image: url("../img/icons/zig-zag/zigzag-fill-tablet.svg");
    background-position-x: center;
    background-size: auto 23.8px;
  }
}
@media (min-width: 1150px) {
  .categories__zig-zag::after {
    height: 34px;
    background-size: 1250px;
  }
}

