  .categories__container {
    background-image: url("../img/background/index-intro-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .categories__container {
    padding-top: 111px;
    padding-bottom: 371px;
    background-image: url("../img/background/index-intro-tablet@1x.jpg");
    background-position: 50% -45px;
