.categories__item {
  position: relative;
  background-color: #63d1bb;
}
.categories__item:nth-child(even) {
  background-color: #56c3b2;
}
.categories__item:nth-child(even) .categories__link:hover {
  background-color: #46c1ae;
  transform: translateY(-5px);
}
.categories__item:nth-child(even) .categories__link:hover::after {
  opacity: 0.7;
}
.categories__item:nth-child(even) .categories__link:active::after {
  opacity: 0.3;
}
