.catalog-item__button:hover svg {
  opacity: 1;
  fill: #63d1bb;
}
.catalog-item__button:focus svg {
  opacity: 1;
  fill: #63d1bb;
}
.catalog-item__button:active svg {
  opacity: 0.3;
}

.manufacturing {
  padding: 0 30px;
  padding-bottom: 58px;
}
@media (min-width: 768px) {
  .manufacturing {
    padding-bottom: 64px;
    font-size: 50px;
  }
}
@media (min-width: 1150px) {
  .manufacturing {
    padding-top: 2px;
    padding-bottom: 105px;
    font-size: 50px;
  }
}

.manufacturing__inner {
  position: relative;
  min-width: 260px;
  max-width: 260px;
  margin: 0 auto;
  border: 1px solid #ececec;
}
.manufacturing__inner:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.manufacturing__inner:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .manufacturing__inner {
    display: grid;
    max-width: 668px;
    grid-template-areas: "player-container" "tittle" "manufacturing__order";
  }
}
@media (min-width: 1150px) {
  .manufacturing__inner {
    display: grid;
    max-width: 1050px;
    grid-template-areas: "player-container tittle" "player-container manufacturing__order";
    grid-template-columns: 65% 1fr;
  }
}

.manufacturing__title {
  position: relative;
  margin: 0;
  padding: 20px 13px 27px 91px;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  grid-area: tittle;
}
.manufacturing__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 32px;
  width: 32px;
  height: 22px;
  background-image: url("../img/icons/catalog/video.svg");
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .manufacturing__title {
    padding: 38px 13px 27px 106px;
    font-size: 20px;
  }
  .manufacturing__title::before {
    left: 41px;
  }
}
@media (min-width: 1150px) {
  .manufacturing__title {
    padding: 96px 70px 0 80px;
  }
  .manufacturing__title::before {
    top: 46px;
    left: 79px;
    transform: translateY(0);
  }
}

.manufacturing__descriprion {
  margin: 0 0 45px;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  color: #444444;
}
@media (min-width: 768px) {
  .manufacturing__descriprion {
    margin-bottom: 40px;
    font-size: 15px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .manufacturing__descriprion {
    margin-bottom: 55px;
  }
}

.manufacturing__order {
  padding: 32px 32px 31px;
  grid-area: manufacturing__order;
}
@media (min-width: 768px) {
  .manufacturing__order {
    padding: 0 42px 46px 42px;
  }
}
@media (min-width: 1150px) {
  .manufacturing__order {
    padding: 0 36px 38px 80px;
  }
}

.manufacturing__button {
  width: 100%;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .manufacturing__button {
    max-width: 584px;
    font-size: 17px;
  }
}
@media (min-width: 1150px) {
  .manufacturing__button {
    max-width: 230px;
  }
}

.manufacturing__player-container {
  position: relative;
  grid-area: player-container;
}

.manufacturing__player-button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 77px;
  padding: 0;
  background-color: transparent;
  border: none;
  transform: translateX(-50%) translateY(-50%);
  cursor: pointer;
}
.manufacturing__player-button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-image: url("../img/icons/catalog/play.svg");
  transform: translateX(-50%) translateY(-50%);
}
.manufacturing__player-button:hover {
  transform: translateX(-50%) translateY(-50%) scale(1.2);
}
.manufacturing__player-button:active {
  transform: translateX(-50%) translateY(-50%) scale(0.8);
  opacity: 0.3;
}

.zig-zag {
  max-width: 1050px;
  background-image: url("../img/icons/zig-zag/zigzag-line-mobile.svg");
  background-repeat: repeat-x;
  background-position: bottom;
}
@media (min-width: 768px) {
  .zig-zag {
    background-image: url("../img/icons/zig-zag/zigzag-line-tablet.svg");
  }
}
@media (min-width: 1150px) {
  .zig-zag {
    background-image: url("../img/icons/zig-zag/zigzag-line-desktop.svg");
  }
}